(function () {
    "use strict";

    /** @ngInject */
    function SignupController(authService, $timeout, $state) {
        var vm = this;

        vm.savedSuccessfully = false;
        vm.message = "";

        vm.registration = {
            userName: "",
            password: "",
            confirmPassword: ""
        };

        vm.signUp = function () {

            authService.saveRegistration(vm.registration).then(function (response) {

                vm.savedSuccessfully = true;
                vm.message = "Пользователь успешно зарегистрирован, вы будете направлены на страницу логина через 2 секунды.";
                startTimer();

            },
            function (response) {
                 var errors = [];
                 for (var key in response.data.modelState) {
                     for (var i = 0; i < response.data.modelState[key].length; i++) {
                         errors.push(response.data.modelState[key][i]);
                     }
                 }
                 vm.message = "Пользователь не зарегистрирован из-за: " + errors.join(" ");
             });
        };

        function startTimer() {
            var timer = $timeout(function () {
                $timeout.cancel(timer);
                $state.go("login");
            }, 2000);
        }
    }

    angular
        .module("wrlUi.security")
        .controller("SignupController", SignupController);

    //SignupController.$inject = ["authService", "$timeout", "$state"];
})();

(function () {
    "use strict";

    /** @ngInject */
    function LoginController(authService, $timeout, $state) {
        var vm = this;

        vm.message = "";

        vm.loginData = {
            userName: "",
            password: ""
        };

        vm.login = function () {

            authService.login(vm.loginData).then(function (response) {
                $state.go("apartments");
            },
             function (err) {
                 vm.message = err.error_description;
             });
        };
    }

    angular
        .module("wrlUi.security")
        .controller("LoginController", LoginController);

    //LoginController.$inject = ["authService", "$timeout", "$state"];
})();

